import React from 'react';

const Typing = (e) => {
    return (
        <div>
            {e.typing ? (
                <div>
                    <span style={{position: 'relative', top: 1}}>печатает</span>
                    <div className="typing__loading Loading">
                        <div className="Loading__items">
                            <div className="Loading__item Loading__item_0"></div>
                            <div className="Loading__item Loading__item_1"></div>
                            <div className="Loading__item Loading__item_2"></div>
                        </div>
                    </div>
                </div>
            ) : (
                <span>был в сети недавно</span>
            )}
        </div>
    )
}

export default Typing;