import React from "react";

const Message = (e) => {
    let date = new Date(e.message.time*1000);
    let hours = date.getHours();
    let minutes = date.getMinutes();
    if(hours < 10){
        hours = "0"+hours;
    }
    if(minutes < 10){
        minutes = "0"+minutes;
    }
    return (
        <div onClick={e.openMessageModal} className={`message ${e.message.sent ? 'sent' : 'received'}`}><span>{e.message.text}<div className="messageDate">
            {hours+'.'+minutes}</div></span></div>
    )
}

export default Message;