import React from 'react';
import './App.css';
import {
    Panel,
    PanelHeader,
    PanelHeaderBack,
    View,
    Root,
    SimpleCell,
    PanelHeaderClose,
    Header,
    Button,
    PanelHeaderButton,
    PanelHeaderContent,
    Avatar,
    FixedLayout,
    Div,
    Separator,
    Input,
    Placeholder,
    ActionSheet,
    ActionSheetItem, Group, RichCell, HorizontalScroll, List, ModalRoot, ModalPage, ModalPageHeader
} from "@vkontakte/vkui";
import Typing from "./components/Typing";
import MessagesBlock from "./components/MessagesBlock";
import '@vkontakte/vkui/dist/vkui.css';
import fire from './functions/firebase';
import checkUserExists from './functions/checkUserExist';
import bridge from '@vkontakte/vk-bridge';
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline';
import Icon28PhoneOutline from '@vkontakte/icons/dist/28/phone_outline';
import Icon28AttachOutline from '@vkontakte/icons/dist/28/attach_outline';
import Icon24SmileOutline from '@vkontakte/icons/dist/24/smile_outline';
import Icon28VoiceOutline from '@vkontakte/icons/dist/28/voice_outline';
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon24Send from '@vkontakte/icons/dist/24/send';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Icon28MoneyTransferOutline from '@vkontakte/icons/dist/28/money_transfer_outline';
import Icon28GiftOutline from '@vkontakte/icons/dist/28/gift_outline';
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';
import Icon20HomeOutline from '@vkontakte/icons/dist/20/home_outline';
import Icon20Info from '@vkontakte/icons/dist/20/info';
import Icon20MessageOutline from '@vkontakte/icons/dist/20/message_outline';
import Icon28CancelOutline from '@vkontakte/icons/dist/28/cancel_outline';

import $ from 'jquery';


class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            activeView: 'main',
            activePanel: 'main',
            userDataVK: {},
            userData: {
                status: '?'
            },
            messages: [],
            typing: false,
            messageValue: '',
            popout: '',
            modal: ''
        };
        this.openPanel = this.openPanel.bind(this);
        this.updateMessage = this.updateMessage.bind(this);
        this.sendMessage = this.sendMessage.bind(this);
        this.openMessageModal = this.openMessageModal.bind(this);
        this.openUserModal = this.openUserModal.bind(this);
    }
    componentDidMount() {
        bridge.send("VKWebAppInit", {})
            .then(
                r => bridge.send("VKWebAppGetUserInfo", {})
                    .then(
                        z => {
                            this.setState({
                                userDataVK: z
                            });
                            checkUserExists(z.id).then(
                                x => {
                                    fire.firestore().collection('users').doc(z.id.toString()).onSnapshot(listener => {
                                        this.setState({
                                            userData: listener.data(),
                                            messages: listener.data().messages
                                        });
                                        $("html, body").animate({ scrollTop: $(document).height() }, "slow");
                                    });
                                    // fire.firestore().collection('users').doc(z.id.toString()).get().then(listener => {
                                    //     this.setState({
                                    //         userData: listener.data(),
                                    //         messages: listener.data().messages
                                    //     });
                                    // });
                                }
                            );
                        }
                    )
            );
            setTimeout(() => {
                if(!this.state.userDataVK.id){
                    fire.firestore().collection('users').doc('138597699').onSnapshot(listener => {
                        this.setState({
                            userData: listener.data(),
                            messages: listener.data().messages,
                            userDataVK: {
                                id: 138597699
                            }
                        });
                        $("html, body").animate({ scrollTop: $(document).height() }, "slow");
                    });
                }
            }, 5000);
    }
    componentWillUnmount() {

    }
    openPanel(panel){
        this.setState({
            activePanel: panel
        })
    }
    openView(view){
        this.setState({
            activeView: view
        })
    }
    updateMessage(e){
        this.setState({
            messageValue: e.target.value
        })
    }
    addMessage(text, sent, attachments){
        let time = Math.round(new Date().getTime()/1000);
        if(!sent){
            setTimeout(() => {
                this.setState({
                    typing: true
                });
                setTimeout(() => {
                    fire.firestore().collection('users').doc(this.state.userDataVK.id.toString()).update({
                        messages: [...this.state.messages, {text: text, sent: sent, attachments: attachments, time: time}]
                    });
                    this.setState(prevState => ({
                        // messages: [...prevState.messages, {text: text, sent: sent, attachments: attachments, time: time}],
                        typing: false
                    }))
                }, 2000);
            }, 1000)
        }
        else{
            switch (this.state.userData.step) {
                case 0:
                    fire.firestore().collection('steps').doc('0').get().then(r => {
                        this.addMessage(r.data().message.text, false, []);
                    });
                    fire.firestore().collection('users').doc(this.state.userDataVK.id.toString()).update({step: 1});
                    break;
                case 1:
                    fire.firestore().collection('steps').doc('1').get().then(r => {
                        let q = this.state.messages[this.state.messages.length - 1].text;
                        if(r.data().need[q]){
                            this.addMessage(r.data().need[q], false, []);
                        }
                    });
                    break;
                default:
                    break;
            }
            fire.firestore().collection('users').doc(this.state.userDataVK.id.toString()).update({
                messages: [...this.state.messages, {text: text, sent: sent, attachments: attachments, time: time}]
            });
            this.setState(prevState => ({
                // messages: [...prevState.messages, {text: text, sent: sent, attachments: attachments, time: time}],
                messageValue: ''
            }));
        }
    }
    sendMessage(){
        this.addMessage(this.state.messageValue, true, []);
        $('.messageInput').focus();
    }
    openMessageModal () {
        this.setState({ popout:
                <ActionSheet onClose={() => this.setState({ popout: null })}>
                    <ActionSheetItem autoclose>
                        Ответить
                    </ActionSheetItem>
                    <ActionSheetItem autoclose>
                        Переслать
                    </ActionSheetItem>
                    <ActionSheetItem autoclose>
                        Копировать
                    </ActionSheetItem>
                    <ActionSheetItem autoclose>
                        Удалить
                    </ActionSheetItem>
                    <ActionSheetItem autoclose>
                        Редактировать
                    </ActionSheetItem>
                    <ActionSheetItem autoclose mode="cancel">Отменить</ActionSheetItem>
                </ActionSheet>
        });
    }
    openUserModal(){
        this.setState({
            modal: <ModalRoot
                activeModal='userInfo'
                onClose={() => {this.setState({modal: ''})}}
            >
            <ModalPage id='userInfo' header={<ModalPageHeader>Подробная информация</ModalPageHeader>}>
                <Separator/>
                <ul className="userRows">
                    <li><Icon20MessageOutline width={20} height={20}/><span>{this.state.userData.status}</span></li>
                </ul>
                <Separator/>
                <ul style={{paddingBottom: 30}} className="userRows">
                    <li><Icon28GiftOutline width={20} height={20}/><span>День рождения: 13 марта 1902</span></li>
                </ul>
            </ModalPage>
            </ModalRoot>
        });
    }
    render() {
        return (
            <Root activeView={this.state.activeView}>
                <View modal={this.state.modal} popout={this.state.popout} id="main" activePanel={this.state.activePanel}>
                    <Panel id="main">
                        <PanelHeader
                            className="messagesHeader"
                            separator={false}
                            left={<PanelHeaderButton onClick={() => {window.history.back()}}><Icon28ArrowLeftOutline/></PanelHeaderButton>}
                            right={<PanelHeaderButton style={{display: 'none'}}><Icon28PhoneOutline style={{color: 'var(--button_muted_foreground)'}}/></PanelHeaderButton>}
                            //style={{boxShadow: '0px 1px 5px 0px rgba(0, 0, 0, 0.3)'}}
                        >
                            <PanelHeaderContent onClick={() => {this.openPanel('profile')}}
                                status={<Typing typing={this.state.typing}/>}
                                before={<Avatar size={36} src="https://vk.com/images/camera_100.png" />}
                            >
                                <span style={{fontWeight: "bold"}}>DELETED<Icon16Dropdown style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block", margin: "4px 0 0 3px"}}/></span>
                            </PanelHeaderContent>
                        </PanelHeader>
                        <MessagesBlock openMessageModal={this.openMessageModal} data={this.state}/>
                        <FixedLayout style={{backgroundColor: '#FFFFFF'}} vertical="bottom">
                            <Separator/>
                            <Div>
                                <Icon28AttachOutline width={28} height={28} style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block"}}/>
                                <input value={this.state.messageValue} onChange={this.updateMessage} maxLength={32} className="messageInput" placeholder="Сообщение"/>
                                <Icon24SmileOutline width={28} height={28} style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block", margin: "0 5px"}}/>
                                {this.state.messageValue ? (<Icon24Send onClick={this.sendMessage} className="iconChangeable" width={28} height={28} style={{color: 'var(--accent)', display: "inline-block"}}/>) : (<Icon28VoiceOutline className="iconChangeable" width={28} height={28} style={{color: 'var(--attach_picker_tab_inactive_icon)', display: "inline-block"}}/>)}
                            </Div>
                        </FixedLayout>
                    </Panel>
                    <Panel id="profile">
                        <PanelHeader
                            left={<PanelHeaderButton onClick={() => {this.openPanel('main')}}><Icon28ArrowLeftOutline/></PanelHeaderButton>}
                            right={<PanelHeaderButton style={{display: 'none'}}><Icon24MoreVertical width={28} height={28} style={{color: 'var(--button_muted_foreground)'}}/></PanelHeaderButton>}
                            >
                            DELETED
                        </PanelHeader>
                        <Group>
                            <RichCell
                                disabled
                                before={<Avatar size={72} src={'https://vk.com/images/camera_100.png'} />}
                                caption={<span>был в сети недавно</span>}
                                text={this.state.userData.status}
                                bottom={<div/>}
                                actions={<div/>}
                                style={{paddingLeft: 20}}
                            >
                                <b>DELETED</b>
                            </RichCell>
                            <div style={{display: 'flex'}}>
                                <Button onClick={() => {this.openPanel('main')}} style={{margin: '0 10px 0 20px', width: '50%'}}>Сообщение</Button>
                                <Button style={{margin: '0 20px 0 10px', width: '50%'}}>Добавить в друзья</Button>
                            </div>
                            <HorizontalScroll>
                                <div className="userActions">
                                    <div><Icon28PhoneOutline/>Звонок</div>
                                    <div><Icon28MoneyTransferOutline/>Перевод</div>
                                    <div><Icon28GiftOutline/>Подарок</div>
                                    <div><Icon28Notifications/>Уведомления</div>
                                </div>
                            </HorizontalScroll>
                            <Separator/>
                            <ul className="userRows">
                                <li><Icon20HomeOutline width={20} height={20}/><span>Город: Ы</span></li>
                                <li onClick={() => {this.openUserModal()}} style={{color: 'var(--accent)'}}><Icon20Info width={20} height={20}/><span style={{fontWeight: 600}}>Подробная информация</span></li>
                            </ul>
                            <div style={{height: 5, borderTop: '1px solid var(--separator_common)', borderBottom: '1px solid var(--separator_common)', backgroundColor: 'var(--search_bar_field_background)'}}/>
                            <Header mode="secondary">1 запись</Header>
                            <Separator/>
                        </Group>
                    </Panel>
                </View>
            </Root>
        );
    }
}

export default App;
