import React from "react";
import {Placeholder} from "@vkontakte/vkui";
import Message from "./Message";

function getRandomInt(max) {
    return Math.floor(Math.random() * Math.floor(max));
}

class MessagesBlock extends React.Component{
    constructor(props) {
        super(props);
        this.state = this.props.data;
    }

    render() {
        return (
                this.props.data.messages.length ?
                    (<div className="messageWrap"
                          // style={{paddingTop: 'calc(100vh - 114px - '+this.props.data.messages.length*52+'px)'}}
                    >
                        <div className="messages">
                            {this.props.data.messages.map(v => <Message openMessageModal={this.props.openMessageModal} key={getRandomInt(26789)+v.text} message={v}/>)}
                        </div>
                    </div>) :
                    (<Placeholder stretched><b>Пока нет сообщений</b><br/>Начните общаться!</Placeholder>)


        )
    }
}

export default MessagesBlock;